import { Address } from "nem-library";
import { getDivided, roundAt } from "../../ts/numberUtils";
import { getXemRate } from "../../ts/exchangeUtils";
import { Decimal } from 'decimal.js';
import DialogBasic from "../dialog/Basic";
import { TransactionBriefInfo } from "../../ts/transactionUtils";
import { getAccountHttp } from "../../ts/nisUtils";
export default {
    components: { DialogBasic },
    name: "HomeView",
    props: {
        needsUpdate: true
    },
    data() {
        return {
            styles: {
                xemAmount: {
                    fontSize: 'x-large',
                    textAlign: 'left',
                    marginLeft: '16px'
                },
                xemUnit: {
                    textAlign: 'right',
                    marginRight: '16px',
                    color: Theme.textDarkGray
                },
                jpyAmount: {
                    textAlign: 'right',
                    marginTop: '-8px',
                    marginRight: '16px',
                    color: Theme.textDarkGray
                },
                transactionIcon: {
                    backgroundColor: Theme.nemBlue,
                    padding: '16px',
                    marginTop: '-8px',
                    marginRight: '16px',
