            accountHttp.allTransactions(address).subscribe(transactions => {
                this.transactions = transactions
                    .map(transaction => TransactionBriefInfo.fromTransaction(transaction, address))
                    .filter(info => info != null);
            });
        }
    },
    mounted() {
        this.update();
    },
    watch: {
        needsUpdate: function (needs) {
