<template>
  <div>
    <v-toolbar app>
      <v-toolbar-title>{{ $t('lesson.user_title') }}</v-toolbar-title>
      <v-spacer></v-spacer>
    </v-toolbar>

    <v-content app>
      <img src="../../assets/heroimage_seclesson_large.png" style="width: 100%">
      <v-layout raw wrap style="margin-bottom: 40px">
        <v-flex xs10 offset-xs1>
          <p class="text-xs-center">
            <span v-for="sentence in $t('lesson.user_message')">{{ sentence }}<br></span>
          </p>
        </v-flex>
      </v-layout>

      <v-layout class="app-bottom-container" column align-center>
        <v-btn color="primary" small to="/lesson/key/caution">OK</v-btn>
      </v-layout>
    </v-content>
  </div>

</template>

<script>
  export default {
    name: "Lessonuser"
  }
</script>



