import Theme from '../../raccoonTheme';
import { Address } from "nem-library";
import { getDivided } from "../../ts/numberUtils";
import WalletBar from '../WalletBar.vue';
import { MosaicInfo } from '../../ts/MosaicInfo';
import { getAccountHttp, getMosaicHttp } from "../../ts/nisUtils";
export default {
    components: { WalletBar },
    name: "Balance",
    data() {
        return {
            styles: {
                xemAmount: {
                    fontSize: 'x-large',
                    textAlign: 'left',
                    marginLeft: '16px'
                },
                xemUnit: {
                    textAlign: 'right',
                    marginRight: '16px',
                    color: Theme.textDarkGray
                },
            },
            balance: 0,
            mosaics: [],
        };
    },
    mounted() {
        const accountHttp = getAccountHttp();
        const address = new Address(this.$store.state.activeAccount.address);
        // Balance
        accountHttp.getMosaicOwnedByAddress(address).subscribe(mosaics => {
            this.mosaics = [];
            const mosaicHttp = getMosaicHttp();
            for (let mosaic of mosaics) {
                if (mosaic.mosaicId.name === "xem" && mosaic.mosaicId.namespaceId === "nem") {
                    this.balance = getDivided(mosaic.quantity, 6);
                    this.mosaics.push(new MosaicInfo(mosaic.mosaicId.namespaceId, mosaic.mosaicId.name, mosaic.quantity, 6));
                }
                else {
                    mosaicHttp.getMosaicDefinition(mosaic.mosaicId).subscribe(mosaicDefinition => this.mosaics.push(MosaicInfo.from(mosaic, mosaicDefinition)), error => { });
                }
            }
        }, error => {
            this.balance = 0;
            this.mosaics = [];
        });
    },
};



