<template>
  <div style="background-color: #606060; position: absolute; top: 0; bottom: 0; left: 0; right: 0">
    <v-toolbar app>
      <v-toolbar-title>{{ $t('common.security_lesson') }}</v-toolbar-title>
      <v-spacer></v-spacer>
    </v-toolbar>

    <v-content app>
      <img src="../../assets/heroimage_wallet_small.png" style="width: 100%">
      <v-layout raw wrap style="margin-bottom: 40px">
        <v-flex xs12>
          <v-layout raw justify-center align-center>
            <img width="16" height="16" src="../../assets/icon_caution.png">
            <span style="color: white">{{$t('lesson.key_caution_title') }}</span>
          </v-layout>
        </v-flex>

        <v-flex xs10 offset-xs1 style="margin-top: 8px">
          <p class="text-xs-center">
            <span v-for="sentence in $t('lesson.key_caution_message')" style="color: white">{{ sentence }}<br></span>
          </p>
        </v-flex>
      </v-layout>

      <v-layout class="app-bottom-container" column align-center>
        <v-btn color="primary" small to="/lesson/key">OK</v-btn>
      </v-layout>
    </v-content>
  </div>
</template>

<script>
  export default {
    name: "LessonPrivateKeyCaution",
  }
</script>



