<template>
  <v-app>
    <router-view></router-view>
    <notifications position="bottom center" classes="toast-style" ></notifications>
  </v-app>
</template>

<script>
export default {
  name: 'app',
}
</script>

<style lang="stylus">
  @import './stylus/main'

  .toast-style {
    padding: 10px;
    margin: 0 5px 5px;

    font-size: 12px;
    text-align center

    color: #ffffff;
    background: #808080;
    border-left: 0 solid #187FE7;

    &.app-error {
      background: #808080;
      border-left-color: #808080;
    }
  }
</style>



