<template>
  <div>
    <v-toolbar app>
      <v-toolbar-title>{{ $t('wallet.login_end_title') }}</v-toolbar-title>
      <v-spacer></v-spacer>
    </v-toolbar>

    <v-content app>
      <img src="../../assets/heroimage_login_large.png" style="width: 100%">
      <v-layout raw wrap>
        <v-flex xs10 offset-xs1>
          <p class="text-xs-center">
            <span v-for="sentence in $t('wallet.login_end_message')">{{ sentence }}<br></span>
          </p>
        </v-flex>
        <v-flex>
          <v-layout column align-center>
            <v-btn color="primary" to="/lesson/introduction">{{ $t('common.security_lesson') }}</v-btn>
            <v-btn color="grey" dark to="/top">HOME</v-btn>
          </v-layout>
        </v-flex>

      </v-layout>
    </v-content>
  </div>
</template>

<script>
  export default {
    name: "WalletLoginEnd"
  }
</script>

<style scoped>

</style>



