export default {
    name: "WalletBar",
    props: {
        isOpened: Boolean
    },
    methods: {
        onClickWallet() {
            if (this.isOpened) {
                this.$router.go(-1);
            }
            else {
                this.$router.push('/wallet/select');
            }
        },
        getActiveAccountName() {
            const activeAccount = this.$store.state.activeAccount;
            if (activeAccount === null) {
                return "";
            }
            else {
                return activeAccount.name;
            }
        }
    }
};



