export class InvoiceData {
    constructor(addr, amount = 0) {
        this.address = addr.split('-').join('');
        this.amount = amount;
    }
    toJsonString() {
        const json = {
            v: 2,
            type: 2,
            data: {
                name: "",
                addr: this.address,
                amount: this.amount,
                msg: ""
            }
        };
        return JSON.stringify(json);
    }
}



