import WalletBar from '../WalletBar.vue';
import DialogBasic from '../dialog/Basic.vue';
import DialogPin from "../dialog/Pin.vue";
import Theme from '../../raccoonTheme';
import { Account, Address, EmptyMessage, PlainMessage, TimeWindow, TransferTransaction, XEM } from "nem-library";
import { getDivided } from "../../ts/numberUtils";
import { MosaicInfo } from "../../ts/MosaicInfo";
import { decryptKey } from "../../ts/storage";
import { getTransactionHttp } from "../../ts/nisUtils";
export default {
    components: { WalletBar, DialogBasic, DialogPin },
    name: "SendConfirmation",
    data() {
        return {
            showSendCaution: true,
            sheet: true,
            styles: {
                bottomBorder: {
                    width: '100%',
                    height: '4px',
                    backgroundColor: Theme.primary
                }
            },
            showPinDialog: false,
            progress: true
        };
    },
    methods: {
        calculateFee() {
            try {
                const transaction = this.createTransaction();
                return getDivided(transaction.fee, 6).toNumber();
            }
            catch (_a) {
                return 0;
            }
        },
        createTransaction() {
            const xemName = MosaicInfo.xem(0).fullName;
            console.log(this.$store.state.sendMosaics);
            let message;
            if (this.$store.state.sendMessage.length == 0) {
                message = EmptyMessage;
            }
            else if (this.$store.state.usesMessageEncryption) {
                message = EmptyMessage;
                //message = account.encryptMessage(this.$store.state.sendMessage);
            }
            else {
                message = PlainMessage.create(this.$store.state.sendMessage);
            }
            if (Object.keys(this.$store.state.sendMosaics).length == 1 && xemName in this.$store.state.sendMosaics) {
                // XEM transfer
                return TransferTransaction.create(TimeWindow.createWithDeadline(), new Address(this.$store.state.receiverAddress), new XEM(this.$store.state.sendMosaics[xemName].amount), message);
            }
            else {
                // Mosaic transfer
                let mosaics = [];
                for (let key in this.$store.state.sendMosaics) {
                    const mosaicInfo = this.$store.state.sendMosaics[key];
                    mosaics.push(mosaicInfo);
                }
                return TransferTransaction.createWithMosaics(TimeWindow.createWithDeadline(), new Address(this.$store.state.receiverAddress), mosaics, message);
            }
        },
        onPassed(pin) {
            let account;
            try {
                const senderAddress = this.$store.state.activeAccount.address;
                const key = decryptKey(senderAddress, pin);
                if (key == null) {
                    console.log('Failed to decrypt secret.');
                    return;
                }
                account = Account.createWithPrivateKey(key);
            }
            catch (error) {
                this.$notify({ text: error.toString(), position: 'bottom center', type: 'app-error' });
                return;
            }
            const transaction = this.createTransaction();
            const signedTransaction = account.signTransaction(transaction);
            const transactionHttp = getTransactionHttp();
            transactionHttp.announceTransaction(signedTransaction).subscribe(result => {
                if ((result.code === 1) && (result.type === 1)) {
                    this.$router.push('/send/complete');
                }
                else {
                    this.$notify({ text: result.message, position: 'bottom center', type: 'app-error' });
                }
            }, error => {
                this.$notify({ text: error.toString(), position: 'bottom center', type: 'app-error' });
            });
        }
    }
};



