import { Address } from "nem-library";
import DialogBasic from '../dialog/Basic.vue';
import MUTATION_TYPES from '../../ts/mutationTypes';
import { getAccountHttp } from "../../ts/nisUtils";
export default {
    components: { DialogBasic },
    name: "SendView",
    data() {
        return {
            address: "",
            showNewbieCaution: false,
        };
    },
    methods: {
        onClickedOk() {
            const accountHttp = getAccountHttp();
            let address;
            try {
                address = new Address(this.address);
            }
            catch (_a) {
                this.$notify({
                    text: this.$t('common.invalid_address'),
                    position: 'bottom center',
                    type: 'app-error'
                });
                return;
            }
            this.$store.commit(MUTATION_TYPES.SET_RECEIVER_ADDRESS, this.address);
            // check the address is newbie
            accountHttp.getFromAddress(address).subscribe(accountInfoWithMetaData => {
                this.$router.push('/send/amount');
            }, error => {
                this.showNewbieCaution = true;
            });
        },
        onClosedNewbieCaution() {
            this.$router.push('/send/amount');
        }
    }
};



